.skillCard {
  padding: 20px;
  border-radius: 8px;
  background: var(--bg-theme2);
  box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.1);
  color: var(--generalText);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  transition: 400ms;
  cursor: default;
  border: 1px solid var(--theme1);
  &:hover {
    box-shadow: unset;
  }
  .skillIcon {
    width: 35px;
    height: 35px;
    margin: 0 auto;
    margin-bottom: 8px;
    border: 1px solid var(--theme1);
    border-radius: 6px;
    padding: 3px;
    // box-shadow: 0px 0px 10px 10px rgba(0, 0, 0, 0.1);
    path {
      fill: var(--theme1);
      width: 100%;
      height: 100%;
    }
  }
  .skillTitle {
    text-align: center;
    font-size: 20px;
    margin-bottom: 8px;
    font-weight: 550;
  }
  .skillDescription {
    text-align: center;
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
  }
}
